
header {
  background: #563d7c;
}
.container-fluid nav.sidebar ul {
  margin: 0;
  padding: 0;
}
.container-fluid nav.sidebar ul li {
    list-style: none;
}
.container-fluid nav.sidebar ul li a {
      text-decoration: none;
      display: block;
      padding: .25rem 1.5rem;
      font-size: 90%;
      color: rgba(0, 0, 0, 0.65);
}
#content {
  padding-top: 1rem;
}
#content .product {
    -webkit-box-shadow: 1px 0px 1px 2px rgba(0, 1, 1, 0.06);
            box-shadow: 1px 0px 1px 2px rgba(0, 1, 1, 0.06);
    margin-bottom: 2rem;
    border-radius: .2rem;
    overflow: hidden;
}
#content .product .product__block {
      overflow: hidden;
}
#content .product .product__block .block__img {
        height: 238px;
        overflow: hidden;
}
#content .product .product__block .block__img:hover img {
          -webkit-transform: scale(1.1);
                  transform: scale(1.1);
}
#content .product .product__block .block__img img {
          width: 100%;
          height: auto;
          -webkit-transition: all .3s;
          transition: all .3s;
}
#content .product .product__block .block__context {
        padding: .9rem .5rem .5rem .4rem;
}
#content .product .product__block .block__context p {
          line-height: 0.5;
}
#content .product .product__block .block__context p.title {
          text-transform: uppercase;
          font-weight: bold;
}
#content .product .product__block .block__context .d-flex {
          -webkit-box-pack: justify;
              -ms-flex-pack: justify;
                  justify-content: space-between;
          padding: .5rem .5rem;
}
#content .product .product__block .block__context .d-flex span.view {
            padding-left: .5rem;
            cursor: pointer;
            text-decoration: underline;
            font-weight: bold;
}
#content .product .product__block .block__context .d-flex span.price {
            display: inline;
            color: #FF0036;
}
.product-detail {
  margin-top: 1.7rem;
}
.product-detail .product-detail__img .img img {
    width: 100%;
}
.product-detail .product-detail__context .context p {
    font-weight: bold;
    line-height: .7;
}
.product-detail .product-detail__context .context p span {
      font-weight: 400;
}
.load-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.65);
}
.load-container .load {
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.load-container .load .lds-roller {
      display: inline-block;
      position: relative;
      width: 64px;
      height: 64px;
}
.load-container .load .lds-roller div {
      -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
              animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
      -webkit-transform-origin: 32px 32px;
              transform-origin: 32px 32px;
}
.load-container .load .lds-roller div:after {
      content: " ";
      display: block;
      position: absolute;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: #fff;
      margin: -3px 0 0 -3px;
}
.load-container .load .lds-roller div:nth-child(1) {
      -webkit-animation-delay: -0.036s;
              animation-delay: -0.036s;
}
.load-container .load .lds-roller div:nth-child(1):after {
      top: 50px;
      left: 50px;
}
.load-container .load .lds-roller div:nth-child(2) {
      -webkit-animation-delay: -0.072s;
              animation-delay: -0.072s;
}
.load-container .load .lds-roller div:nth-child(2):after {
      top: 54px;
      left: 45px;
}
.load-container .load .lds-roller div:nth-child(3) {
      -webkit-animation-delay: -0.108s;
              animation-delay: -0.108s;
}
.load-container .load .lds-roller div:nth-child(3):after {
      top: 57px;
      left: 39px;
}
.load-container .load .lds-roller div:nth-child(4) {
      -webkit-animation-delay: -0.144s;
              animation-delay: -0.144s;
}
.load-container .load .lds-roller div:nth-child(4):after {
      top: 58px;
      left: 32px;
}
.load-container .load .lds-roller div:nth-child(5) {
      -webkit-animation-delay: -0.18s;
              animation-delay: -0.18s;
}
.load-container .load .lds-roller div:nth-child(5):after {
      top: 57px;
      left: 25px;
}
.load-container .load .lds-roller div:nth-child(6) {
      -webkit-animation-delay: -0.216s;
              animation-delay: -0.216s;
}
.load-container .load .lds-roller div:nth-child(6):after {
      top: 54px;
      left: 19px;
}
.load-container .load .lds-roller div:nth-child(7) {
      -webkit-animation-delay: -0.252s;
              animation-delay: -0.252s;
}
.load-container .load .lds-roller div:nth-child(7):after {
      top: 50px;
      left: 14px;
}
.load-container .load .lds-roller div:nth-child(8) {
      -webkit-animation-delay: -0.288s;
              animation-delay: -0.288s;
}
.load-container .load .lds-roller div:nth-child(8):after {
      top: 45px;
      left: 10px;
}
@-webkit-keyframes lds-roller {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes lds-roller {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}











































































































